import React from "react";
import SlideMenu from "../SlideMenu/SlideMenu";
import './style.css'

class Skills extends React.Component {

    constructor(props, context) {
        super(props, context);
    }

    render() {
        return (
            <>
                <SlideMenu/>
                <main className={"skills"}>
                    Langage de programation en cours d'apprentisage :
                    <ul>
                        <li>
                            Langage bas niveau :
                            <ul>
                                <li>
                                    C/C++ (C préféré,
                                    <a href={"/projet"}> voir projet réaliser)</a>
                                </li>
                            </ul>
                        </li>
                        <li>
                            Langage haut niveau :
                            <ul>
                                <li>
                                    C#, Java, PHP, Python (C# préféré)
                                </li>
                            </ul>
                        </li>
                        <li>
                            Langage web :
                            <ul>
                                <li>
                                    Javascript (html, css inclus)
                                </li>
                            </ul>
                        </li>
                        <li>
                            Framework web :
                            <ul>
                                <li>
                                    React.js, Electron.js (Frontend)<br/>

                                </li>
                                <li>
                                    Node.js (Backend)
                                </li>
                            </ul>
                        </li>
                        <li>
                            Utilitaire :
                            <ul>
                                <li>
                                    Windows (utilisation quotidienne), Linux (préféré), Docker (Linux), MongoDB, MySQL, ide (Jetbrains, visual studio code
                                </li>
                            </ul>
                        </li>
                    </ul>
                </main>
            </>
        )
    }
}

export default Skills;
