import React from "react";
import {slide as Menu} from 'react-burger-menu';
import { Link } from "react-router-dom/";
import "./style.css"

class SlideMenu extends React.Component {
    constructor(props, context) {
        super(props, context);
    }

    render() {
        return (
            <Menu>
                <Link to={"/"} id={"home"} className={"bm-item-list"}>Présentation</Link>
                <Link to={"/training"} id={"training"} className={"bm-item-list"}>Mes formations</Link>
                <Link to={"/skills"} id={"skills"} className={"bm-item-list"}>Mes compétences</Link>
                <Link to={"/project"} id={"project"} className={"bm-item-list"}>Mes projets réalisés</Link>
            </Menu>
        )
    }
}

export default SlideMenu;
