import React from 'react';
import {Button, Modal} from 'react-bootstrap';
import './project.css';

interface MyProps {}

interface MyState {
    buttonTitle: string;
    show: boolean;
    modalTitle: string;
    srcVideo: string;
    projectGithub: string;
}

export default class ProjectTemplate extends React.Component <MyProps, MyState>{
    constructor(props, context) {
        super(props, context);

        this.state = {
            show: false
        }
        this.handleClose = this.handleClose.bind(this);
        this.handleShow = this.handleShow.bind(this);
    }

    handleClose() {
        this.setState({show : false});
    }
    handleShow(){
        this.setState({show : true});
    }

    render() {
        return (
            <>
                <Button variant={"primary"} onClick={this.handleShow}>
                    {this.props.buttonTitle}
                </Button>
                <Modal show={this.state.show} onHide={this.handleClose} size={"xl"} centered={true}>
                    <Modal.Header closeButton>
                        <Modal.Title id={"contained-modal-title-vcenter"} className={"title"}>{this.props.modalTitle}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body size={"xl"} className={"video-responsive"}>
                        <video controls key={this.props.srcVideo}>
                            <source src={this.props.srcVideo} type={"video/mp4"}/>
                            Your browser does not support this video format. :(
                        </video>
                    </Modal.Body>
                    <Modal.Footer>
                        <a href={this.props.projectGithub} target={"_blank"}>
                            <Button variant="primary" onClick={this.handleClose}>
                                Voir le code sur mon github.
                            </Button>
                        </a>
                    </Modal.Footer>
                </Modal>
            </>
        )
    }
}
