import React from "react";
import ChatVideo from "../../../Video/Tchat.mp4"
import PacmanVideo from "../../../Video/Pacman.mp4"
import "./style.css"
import ProjectTemplate from "../ProjectTemplate";
import Card from "react-bootstrap/Card";

class CCPP extends React.Component {

    constructor(props, context) {
        super(props, context);
    }

    render() {
        return (
            <div className={"grid"}>
                <Card style={{ width: '50vh' }}>
                    <Card.Body>
                        <Card.Title className={"title"}>Tchat (Unix) : Langage C</Card.Title>
                        <Card.Text>
                            <p className={"text"}>
                                Dans le cadre de mon stage Août 2020 pour CampusID, j'ai du réalisé un tchat sous unix
                                avec ihm en C (GTK). J'ai du apprendre à utiliser les processus léger d'unix + les sémaphores (première utilisation / découverte).
                                J'ai rencontré une difficulté sur la gestion simultanée des clients (gérer la perte de connection, faire un keep-alive).
                                J'ai su ensuite trouvé une solution toute à fait viable.
                            </p>
                        </Card.Text>
                        <Card.Link href="#">
                            <ProjectTemplate
                                buttonTitle={"Démo"}
                                modalTitle={"Réalisation d'un chat multi-client/serveur de manière synchrone en C (Démo)"}
                                srcVideo={ChatVideo}
                                projectGithub={"https://github.com/Antonin-Artaud/Project-C/tree/master/Socket"}
                            />
                        </Card.Link>
                    </Card.Body>
                </Card>
                <Card style={{ width: '50vh' }}>
                    <Card.Body>
                        <Card.Title className={"title"}>Pacman (Unix) : Langage C</Card.Title>
                        <Card.Text>
                            <p className={"text"}>
                                Voici un projet de première année. Il s'agit d'un jeu coder en C, je me suis inspiré de la base du pacman.
                                J'ai 3 versions de disponible de ce projet l'une via une api fournit par notre professeur, l'une avec GTK/CAIRO, et une dernière
                                qui utilise SDL (dans cette versions absolument tout est fait au plus bas niveau). J'ai utilisé un algorithme pathfinder pour les ennemis
                                pour trouver le chemin le plus court possible entre les ennemis et le joueur à chaque déplacement.
                            </p>
                        </Card.Text>
                        <Card.Link href="#">
                            <ProjectTemplate
                                buttonTitle={"Démo"}
                                modalTitle={"Réalisation d'un jeu sur la base du pacman en C (Démo)"}
                                srcVideo={PacmanVideo}
                                projectGithub={"https://github.com/Antonin-Artaud/Project-C/tree/master/Pacman"}
                            />
                        </Card.Link>
                    </Card.Body>
                </Card>
            </div>
        )
    }
}

export default CCPP;
