import React from 'react';
import './project.css';
import SlideMenu from "../SlideMenu/SlideMenu";
import {Dropdown, DropdownButton} from "react-bootstrap";
import {Link} from "react-router-dom";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import CCPP from "./CCPP/CCPP";

export default class Project extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            element: <p>Aucun projet n'est sélectionné. :(</p>
        }

        this.displayProject = this.displayProject.bind(this);
    }

    displayProject(e) {
        console.log(typeof e.target.value)
        switch (e.target.value) {
            case '1':
                this.setState({element: <CCPP/>});
                console.log(this.state.element);
                break;

            default:
                this.setState({element: <p>Aucun projet n'est disponible. :(</p>})
                break;
        }
    }

    render() {
        return (
            <>
                <SlideMenu/>
                <main className={"wrapperk"}>
                    <DropdownButton as={ButtonGroup} title={"Projet réalisé en fonction du langage"} id="bg-vertical-dropdown-1" className={"selectTypeProject"}>
                        <Dropdown.Item eventKey="1">
                            <button className={"list-button"} value={1} onClick={this.displayProject}>C/C++</button>
                        </Dropdown.Item>
                        <Dropdown.Item eventKey="2">
                            <button className={"list-button"} value={2} onClick={this.displayProject}>C#</button>
                        </Dropdown.Item>
                        <Dropdown.Item eventKey="3">
                            <button className={"list-button"} value={3} onClick={this.displayProject}>Java</button>
                        </Dropdown.Item>
                        <Dropdown.Item eventKey="4">
                            <button className={"list-button"} value={4} onClick={this.displayProject}>Javascript</button>
                        </Dropdown.Item>
                        <Dropdown.Item eventKey="5">
                            <button className={"list-button"} value={5} onClick={this.displayProject}>PHP</button>
                        </Dropdown.Item>
                        <Dropdown.Item eventKey="6">
                            <button className={"list-button"} value={6} onClick={this.displayProject}>Python</button>
                        </Dropdown.Item>
                    </DropdownButton>
                    {this.state.element}
                </main>
                <footer/>
            </>
        )
    }
}
