import React from "react";
import SlideMenu from "../SlideMenu/SlideMenu";
import './style.css';
import Card from "react-bootstrap/Card";

class Training extends React.Component {
    constructor(props, context) {
        super(props, context);
    }

    render() {
        return (
            <>
                <SlideMenu/>
                <main className={"wrapperk"}>
                    <h1 className={"formationTitle"}>Mes formations :</h1>
                    <div className={"grid"}>
                        <Card style={{ width: '50vh' }}>
                            <Card.Body>
                                <Card.Title className={"title"}>CampusID/Ynov 2020-2021</Card.Title>
                                <Card.Text>
                                    <p className={"text"}>
                                        Lors de ma deuxième année chez CampusID, mon école a été associée avec Ynov et
                                        devenue Ynov Campus Sophia. Malgré les confinements, j'ai su resté investi dans
                                        mon travail. Cette année m'a fait découvrir de nombreux langages tels que le C++,
                                        C#, JAVA, PHP (plus poussé), Python. Je me suis aussi consacré à apprendre d'autres
                                        technologies en parallèle de mes études comme React.js, Node.js, Electron.js.
                                    </p>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                        <Card style={{ width: '50vh' }}>
                            <Card.Body>
                                <Card.Title className={"title"}>CampusID 2019-2020</Card.Title>
                                <Card.Text>
                                    <p className={"text"}>
                                        J'ai effectué ma première année de bachelor chez CampusID, pendant cette année je me suis beaucoup familiarisé avec le langage
                                        C, ainsi qu'au début du web (html, css, js, php)
                                    </p>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                        <Card style={{ width: '50vh' }}>
                            <Card.Body>
                                <Card.Title className={"title"}>Baccalauréat 2018-2019</Card.Title>
                                <Card.Text>
                                    <p className={"text"}>
                                        J'ai obtenu mon baccalauréat STI2D avec mention assez bien au lycée les Eucalyptus à Nice.
                                    </p>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </div>
                </main>
                <footer/>
            </>
        )
    }
}

export default Training;
