import React from "react";
import SlideMenu from "../SlideMenu/SlideMenu"
import './Style.css'

class Home extends React.Component {

    constructor(props, context) {
        super(props, context);
    }

    render() {
        return (
            <>
                <SlideMenu/>
                <main>
                    <p>Je suis un jeune développeur de 19 ans.
                        J'ai pour passion de concevoir
                        plusieurs sites web ou logiciels à des
                        fins d'apprentissage de nouvelle technologie. Je suis
                        autodidacte en parallèle de mes
                        études. J'aime apprendre et
                        m'intéresser à beaucoup de nouvelles
                        technologies telles que React.js par
                        exemple. Je suis quelqu'un qui va
                        essayer de résoudre des possibles
                        problèmes par moi-même, mais je
                        n'hésite pas à demander de l'aide
                        quand cela s'avère nécessaire</p>
                </main>
            </>
        )
    }
}

export default Home;
